.conCheck {
		display: inline-block;
		margin-left: 5%;
		position: relative;
		padding-left: 35px;
		margin-bottom: 12px;
		cursor: pointer;
		font-size: 0.8em;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
}
	/* Hide the browser's default checkbox */
.conCheck input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
}
	/* Create a custom checkbox */
.checkmark {
		position: absolute;
		top: 0;
		left: 0;
		height: 25px;
		width: 25px;
		background-color: #eee;
}
	/* On mouse-over, add a grey background color */
.conCheck:hover input ~ .checkmark {
		background-color: #ccc;
}
	/* When the checkbox is checked, add a blue background */
.conCheck input:checked ~ .checkmark {
		background-color: #102229;
}
	/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
		content: "";
		position: absolute;
		display: none;
}
	/* Show the checkmark when checked */
.conCheck input:checked ~ .checkmark:after {
		display: inline-block;
}
	/* Style the checkmark/indicator */
.conCheck .checkmark:after {
		left: 9px;
		top: 5px;
		width: 5px;
		height: 10px;
		border: solid white;
		border-width: 0 3px 3px 0;
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
}
.contRadio {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
	/* Hide the browser's default radio button */
.contRadio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
	/* Create a custom radio button */
.checkRadio {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}
	/* On mouse-over, add a grey background color */
.contRadio:hover input ~ .checkRadio {
  background-color: #ccc;
}
	/* When the radio button is checked, add a blue background */
.contRadio input:checked ~ .checkRadio {
  background-color: #2196F3;
}
	/* Create the indicator (the dot/circle - hidden when not checked) */
.checkRadio:after {
  content: "";
  position: absolute;
  display: none;
}
	/* Show the indicator (dot/circle) when checked */
.contRadio input:checked ~ .checkRadio:after {
  display: block;
}
	/* Style the indicator (dot/circle) */
.contRadio .checkRadio:after {
 	top: 9px;
	left: 9px;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: white;
}
